
body {
  background-image: url(C:\Users\rajat\task1\src\Img\5793343.jpg);
  background-size: cover;
  align-items: center;
  text-align: center;
  padding:0%;
}
.ui.form {
  width: 35%;
  height: 250px;
  justify-content: center;
  justify-items: center;
  text-align: center;
  border:1px solid #000000;
  border-radius: 15px;
  padding: 100px;
  background: linear-gradient(to right, #1c1c1c 0%, #888684 100%);
  position: relative;
  display: inline-block;
}
label{
  color: rgb(248, 248, 248);
  
}
.fa-user-o{
  color: #eee;
}
.fa-envelope-o{
  color: #eee;
}
p {
  color:red;
  padding-top:10px;
  text-align: left;
}
p:invalid {
  animation: shake 200ms;
}
@keyframes shake{
  25%{
    transform: translateX((4px));
  }
  50%{
    transform: translateX((-4px));
  }
  75%{
    transform: translateX((4px));
  }
  95%{
    transform: translateX((-4px));
  }
}
div.field{
  font-size: 15px;
  text-align: left;

}

.fa-user-o{
  padding: 5px;
}
.fa-envelope-o{
  padding: 5px;
}

input.input{
  border-radius: 10px;
  align-items: center;
  width: 90%;
  height: 40px;
  padding-left: 10px;
  position: relative;
  display: inline-block;
}
.custom-btn {
  width: 130px;
  height: 40px;
  color: #fff;
  border-radius: 5px;
  padding: 10px 25px;
  font-family: 'Lato', sans-serif;
  background: transparent;
  cursor: pointer;
  transition: all 0.3s ease;
  position: relative;
  display: inline-block;
  box-shadow: inset 2px 2px 2px 0px rgba(255,255,255,.5),
    7px 7px 20px 0px rgba(0,0,0,.1),
    4px 4px 5px 0px rgba(0,0,0,.1);
  outline: none;
 }
 
 .btn-1 {
  background: rgb(6,14,131);
  background: linear-gradient(0deg,rgb(220, 28, 28) 0%, rgb(81, 82, 83) 100%);
   
  border: none;
 }
 
 .btn-1:hover {
  background: rgb(255, 0, 0);
  background: linear-gradient(0deg, rgb(40, 158, 32) 0%, rgb(116, 116, 119) 100%);
 }

div.ui.message.sucess{
  color: rgb(18, 241, 10);
  text-align: center;
  margin-top: 10px;
}
h1{
  color: rgb(200, 219, 219);
  border-bottom: rgb(245, 0, 0);
  font-weight: 500px;
}
h2{
  color: greenyellow;
}